/**
 * Class that manages state for the PayPal related fields and buttons in the front end.
 */
export default class PPCPStateManager {
	constructor( initialState, stateChangeCallback ) {

		this.state = {};
		this.prevState = { ... initialState };
		this.markedDirty = [];

		this.timeoutID = 0;
		this.stateChangeCallback = stateChangeCallback;
		this.isInitialChange = true;

		for ( const key in initialState ) {
			this.set( key, initialState[ key ] );
		}
	}

	/**
	 * Gets a state variable.

	 * @param {string} key Name/key of the state variable.
	 * @returns {*} Returns the value of the specified property.
	 */
	get( key ) {
		return this.state[ key ];
	}

	/**
	 * Sets a state variable and if a change is detected, initiates the timeout to invoke the state changed callback.
	 *
	 * @param {string} key Variable key/name.
	 * @param {*} value    Variable value.
	 */
	set( key, value ) {

		// Abort if no change and property hasn't been marked as dirty
		if( this.state[ key ] === value && ! this.markedDirty.includes( key ) ) {
			return;
		}
		this._maybeClearTimeout();

		this.state[ key ] = value;

		// If no other state prop is changed in the next 0.2 seconds, fire state change event.
		this.timeoutID = setTimeout( this._invokeStateChanged.bind( this ), 200 );
	}

	/**
	 * Manually marks a state variable as dirty, causing the state changed event to be fired next time this state variable is set.
	 *
	 * @param {string} key Name/key of the variable.
	 */
	setDirty( key ) {
		this.markedDirty.push( key );
	}

	/**
	 * Resets previous class variables. To be called after a state changed event is invoked.
	 *
	 * @private
	 */
	_reset() {
		this.prevState = { ...this.state };
		this.timeoutID = 0;
		this.isInitialChange = false;
	}

	/**
	 * Invokes the state changed callback method.
	 *
	 * @private
	 */
	_invokeStateChanged() {
		let dirtyProps = this._getDirtyProps();
		this.markedDirty = []; // Must reset markedDirty before stateChangeCallback.
		this.stateChangeCallback( this.isInitialChange, dirtyProps, this.state, this.prevState );
		this._reset();
	}

	/**
	 * Clears the timeout Id if one is specified.
	 *
	 * @private
	 */
	_maybeClearTimeout() {

		if( this.timeoutID == 0 ) {
			return;
		}

		clearTimeout( this.timeoutID );
		this.timeoutID = 0;
	}

	/**
	 * Returns state variables that have been changed or that have been manually marked as dirty.
	 *
	 * @returns {string[]} Returns an array with keys that have been changed or that have been manually marked as dirty.
	 *
	 * @private
	 */
	_getDirtyProps() {
		let dirty = [];
		for ( const key in this.state ) {
			if ( this.state[ key ] != this.prevState[ key ] || this.markedDirty.includes( key ) ){
				dirty.push( key );
			}
		}
		return dirty;
	}
}
