/* global paypal, gf_check_field_rule, gformIsRecaptchaPending, gformCalculateTotalPrice, gforms_ppcp_frontend_strings */
import PPCPStateManager from './state_manager';

window.GFPPCP = null;

( function ( $ ) {
	var gform = window.gform || {};
	gform.ppcp = gform.ppcp || {};
	window.GFPPCP = function ( args ) {
		for ( let prop in args ) {
			if ( args.hasOwnProperty( prop ) ) {
				this[ prop ] = args[ prop ];
			}
		}

		let self = this;

		self.init = function() {

			self.paypalButtonInstances = [];
			self.paymentAmount = 0;
			self.countryCode = '';
			self.form = '#gform_' + self.formId;
			self.GFCCField = '#field_' + self.formId + '_' + self.ccFieldId;
			self.submitButtonId = '#gform_submit_button_' + self.formId;
			self.paypalButtons = $( '#gform_ppcp_smart_payment_buttons_' + self.formId );
			self.orderIdField = $( '#gf_ppcp_order_id_' + self.formId );
			self.subscriptionIdField = $( '#gf_' + self.formId + '_ppcp_subscription_id' );
			self.paymentMethodField = $( '#gform_wrapper_' + self.formId + ' .gform_ppcp_payment_method select' );
			self.PayPalFunding = paypal.FUNDING.PAYPAL;

			self.applyPriceClass();

			// Initializing state manager.
			const initialState = {
				'currency'      : self.currency,
				'paymentMethod' : self.paymentMethodField.val(),
				'fieldVisible'  : true,
				'submitVisible' : true,
			}

			gform.ppcp.stateManager = new PPCPStateManager( initialState, self.handleStateChange.bind( this ) );

			// Binding hooks, filters and events related to the state manager.
			self.bindStateEvents();

			// Bind submit event for multipage forms.
			$( self.form ).on( 'submit', self.handleFormSubmission );

		};

		/**
		 * Handles state change, updating UI accordingly.
		 *
		 * @since 2.4
		 *
		 * @param {bool}  isInitialChange True if this is the first time the state has been changed. False otherwise.
		 * @param {array} changed         An array with the state keys that were changed since the last state change event ( i.e. ['paymentMethod', 'fieldVisible'] ).
		 * @param {array} state           An associative array with the current state. (i.e. { 'paymentMethod': 'Credit Card', 'buttonVisible' : true, 'fieldVisible' : false, 'activeFeed' : {...} } ).
		 * @param {array} prevState       An associative array with the previous state.
		 *
		 */
		self.handleStateChange = function( isInitialChange, changed, state, prevState ) {

			// If there is no active feed, hide all paypal fields and abort.
			if( ! state['activeFeed'] ) {
				// Passing amount = 0 to hide fields.
				self.showHideFields( state, 0 );
				return;
			}

			// Setting global payment amount.
			self.paymentAmount = self.getPaymentAmount( state['activeFeed'], state['total'] );

			// Handling currency change.
			if( changed.includes('currency') ) {
				self.handleCurrencyChange( isInitialChange, state, prevState );
				return;
			}

			// Handling feed change.
			if( changed.includes( 'activeFeed' ) ) {
				self.handleFeedChange( state );
				return;
			}

			// Handling amount change.
			if( changed.includes( 'total' ) ) {
				self.handleTotalChange( state );
				return;
			}

			// Handling all other changes.
			self.handleVisibilityChange( state );

		}

		/**
		 * Updates the UI based after a change in the current currency.
		 *
		 * @since 2.4
		 *
		 * @param {bool}  isInitialChange True if this is the first time the state has been changed. False otherwise.
		 * @param {array} state           An associative array with the current state. (i.e. { 'paymentMethod': 'Credit Card', 'buttonVisible' : true, 'fieldVisible' : false, 'activeFeed' : {...} } ).
		 * @param {array} prevState       An associative array with the previous state.
		 */
		self.handleCurrencyChange = function( isInitialChange, state, prevState ) {

			self.resetPayPalScript( state['currency'], function() {

				// When script is loaded, adjust UI.

				// Render paypal credit card and smart buttons.
				self.renderPayPalFields( state['activeFeed'], self.paymentAmount );

				// Display fields appropriately.
				self.showHideFields( state, self.paymentAmount );

				// Enable form.
				self.enableForm();

				// Remove spinner.
				self.hideFormSpinner();
			});


		}

		/**
		 * Updates the UI after a change in the active feed.
		 *
		 * @since 2.4
		 *
		 * @param {array} state An associative array with the current state. (i.e. { 'paymentMethod': 'Credit Card', 'buttonVisible' : true, 'fieldVisible' : false, 'activeFeed' : {...} } ).
		 */
		self.handleFeedChange = function( state ) {

			self.resetCardErrors();

			// Render PayPal fields (even if fields and buttons will eventually be hidden)
			self.renderPayPalFields( state['activeFeed'], self.paymentAmount );

			// Display appropriate fields and buttons.
			self.showHideFields( state, self.paymentAmount );
		}

		/**
		 * Updates the UI after a change in visibility (i.e. field conditional logic, payment method or submit button conditional logic changes).
		 *
		 * @since 2.4
		 *
		 * @param {array} state An associative array with the current state. (i.e. { 'paymentMethod': 'Credit Card', 'buttonVisible' : true, 'fieldVisible' : false, 'activeFeed' : {...} } ).
		 */
		self.handleVisibilityChange = function( state ) {

			// Render PayPal fields if they haven't been rendered.
			self.ensurePayPalFieldsRendered( state['activeFeed'], self.paymentAmount );

			// Display appropriate fields and buttons.
			self.showHideFields( state, self.paymentAmount );
		}

		/**
		 * Updates the UI after a change in payment amount.
		 *
		 * @since 2.4
		 *
		 * @param {array} state An associative array with the current state. (i.e. { 'paymentMethod': 'Credit Card', 'buttonVisible' : true, 'fieldVisible' : false, 'activeFeed' : {...} } ).
		 */
		self.handleTotalChange = function( state ) {

			// Render PayPal fields if they haven't been rendered.
			self.ensurePayPalFieldsRendered( state['activeFeed'], self.paymentAmount );

			// Display appropriate fields and buttons.
			self.showHideFields( state, self.paymentAmount );

			// Update payment amount in credit message.
			self.renderCreditMessage( self.paymentAmount );
		}

		/**
		 * Returns an associative array containing payment methods that are selected and supported by the active feed.
		 *
		 * @since 2.4
		 *
		 * @param {object} activeFeed Active feed.
		 *
		 * @returns {array} Returns an associative array of payment methods.
		 */
		self.getPaymentMethods = function( activeFeed ) {

			if ( ! activeFeed.supportedPaymentMethods ) {
				return [];
			}

			let paymentMethods = [];
			Object.values( activeFeed.supportedPaymentMethods ).forEach( function( paymentMethod ){
				const method = paymentMethod.value ? paymentMethod.value : paymentMethod;
				if( Object.values( self.paymentMethods ).includes( method ) ){
					paymentMethods.push( method );
				}
			});

			return paymentMethods;
		}

		/**
		 * Displays the appropriate fields depending on the current state.
		 *
		 * @since 2.4
		 *
		 * @param {array}  state         The current state array.
		 * @param {string} paymentMethod Currently selected payment method.
		 */
		self.showHideFields = function( state, paymentAmount ) {

			let paymentMethods = state['activeFeed'] ? self.getPaymentMethods( state['activeFeed'] ) : [];

			const isPaymentSelectorVisible = self.showHidePaymentMethodSelector( paymentMethods );
			const isCreditCardEnabled = self.isCreditCardEnabled( state['activeFeed'] );
			const hasCreditCardPaymentMethod = isPaymentSelectorVisible || ( state['paymentMethod'] === 'Credit Card' && isCreditCardEnabled );
			const hidePayPalField = ! state['fieldVisible'] || paymentAmount == 0 || ! hasCreditCardPaymentMethod;

			// Display or show main PayPal field based on conditional logic or payment amount.
			const fieldDisplay = hidePayPalField ? 'none' : 'block';
			self.showHidePayPalField( fieldDisplay );

			let creditCardField = $( '#gform_wrapper_' + self.formId + ' .ginput_container_custom_card_fields' );
			if ( state['paymentMethod'] === 'Credit Card' ) {

				const creditCardFieldDisplay = isCreditCardEnabled ? 'block' : 'none';
				creditCardField.css( 'display', creditCardFieldDisplay );

				// Hide paypal button and restore submit button.
				self.paypalButtons.css( 'display', 'none' );
				if ( state['submitVisible'] ){
					self.showHideSubmitButton( 'visible' );
				}
			}
			else {

				// Hide credit card fields.
				creditCardField.hide();

				const hidePayPalButton = ! state['fieldVisible'] || paymentAmount == 0 || ! self.isPayPalButtonsEnabled( state['activeFeed'] );

				// Display PayPal Button if conditional logic and amount > 0 are satisfied.
				const buttonDisplay = hidePayPalButton || ! state['submitVisible'] ? 'none' : 'inline-block';
				self.paypalButtons.css( 'display', buttonDisplay );

				const submitVisibility = hidePayPalButton && state['submitVisible'] ? 'visible' : 'hidden';
				self.showHideSubmitButton( submitVisibility );
			}
		}

		/**
		 * Displays or hides the main PayPal field.
		 *
		 * @since 2.4
		 *
		 * @param {string} display Value to be used for the CSS display property of the field. (i.e. block, none ).
		 */
		self.showHidePayPalField = function( display ) {
			let paypalField = $( '#gform_wrapper_' + self.formId + ' .gform_ppcp_custom_card_fields' );

			let prevDisplay = paypalField.css( 'display' );
			if ( prevDisplay != display ) {
				paypalField.css( 'display', display );
				gform.ppcp.stateManager.setDirty( 'fieldVisible' );
			}

		}
		/**
		 * Displays or hides the submit button based on the specified visibility parameter.
		 *
		 * @since 2.4
		 *
		 * @param {string} visibility Determines the visibility of the submit button. Possible values are "visible" and "hidden".
		 */
		self.showHideSubmitButton = function( visibility ) {
			const submitButton = $( self.submitButtonId );
			submitButton.css( 'visibility', visibility );

			if ( visibility === 'hidden' ) {
				submitButton.css( 'position', 'absolute' );
				submitButton.css( 'left', '-9999em' );
			} else {
				submitButton.css( 'position', 'static' );
				submitButton.css( 'left', '0' );
			}
		};

		/**
		 * Ensures PayPal Buttons and PayPal Credit Card fields are rendered
		 *
		 * @since 2.4
		 *
		 * @param {object} activeFeed    Current active feed.
		 * @param {float}  paymentAmount Amount to be paid.
		 */
		self.renderPayPalFields = function( activeFeed, paymentAmount ) {

			// Render PayPal credit card field only if hasn't been rendered yet.
			const isCreditCardRendered = $( self.form ).data( 'ppcp-card-rendered' );
			if ( self.isCreditCardEnabled( activeFeed ) && ! isCreditCardRendered  ) {

				// Render Credit Card Fields
				self.renderCardFields();
			}

			if( self.isPayPalButtonsEnabled( activeFeed ) ) {

				// Render buttons.
				if( jQuery( '#gform_ppcp_smart_payment_buttons_' + self.formId ).length ) {
					self.renderPayPalButtons( activeFeed.intent, paymentAmount );
				}
			}
		}

		/**
		 * Checks if the hosted card fields are supported.
		 *
		 * @since 2.4
		 *
		 * @param {array} activeFeed  Current active feed.
		 */
		self.isCreditCardEnabled = function( activeFeed ) {
			const paymentMethods = self.getPaymentMethods( activeFeed );

			return typeof paypal !== 'undefined' &&
				paypal.HostedFields.isEligible() &&
				paymentMethods.includes( 'Credit Card' );
		}

		/**
		 * Check if the PayPal Buttons can be displayed.
		 *
		 * @since 2.4
		 *
		 * @param {array} activeFeed  Current active feed.
		 * @returns {bool} True if PayPal buttons can be displayed. False otherwise.
		 */
		self.isPayPalButtonsEnabled = function( activeFeed ) {
			const paymentMethods = self.getPaymentMethods( activeFeed );
			return paymentMethods.includes( 'PayPal Checkout' );
		}

		/**
		 * Renders PayPal credit card and PayPal Buttons if they haven't been rendered yet. Do nothing if they have been rendered.
		 *
		 * @since 2.4
		 *
		 * @param {array} activeFeed    Current active feed.
		 * @param {float} paymentAmount Amount to be paid.
		 */
		self.ensurePayPalFieldsRendered = function( activeFeed, paymentAmount ) {
			if ( ! self.isPayPalButtonsRendered() ) {
				self.renderPayPalFields( activeFeed, paymentAmount );
			}
		}

		/**
		 * Target of gform_product_total filter. Updates the state manager every time the total changes.
		 *
		 * @since 2.4
		 *
		 * @param {float} total  Current form total.
		 * @param {int}   formId Current form id.
		 *
		 * @returns {float} Returns the filtered total.
		 */
		self.handleProductTotalFilter = function( total, formId ) {
			if ( formId != self.formId ) {
				return total;
			}

			gform.ppcp.stateManager.set( 'total', total );
			return total;
		}

		/**
		 * Target of the gform_frontend_feeds_evaluated hook. Updates the state manager every time the frontend feeds get evaluated.
		 *
		 * @since 2.4
		 *
		 * @param {array} feeds  Array of active feeds.
		 * @param {int}   formId Current form id.
		 */
		self.handleFrontendFeedsEvaluated = function( feeds, formId ) {
			if ( formId !== self.formId ) {
				return;
			}
			const activeFeed = self.getActiveFeed( feeds );
			gform.ppcp.stateManager.set( 'activeFeed', activeFeed );

			// See if payment method needs to change as well.
			const paymentMethods = self.getPaymentMethods( activeFeed );
			const paymentMethodChanged = paymentMethods.length > 0 && ! paymentMethods.includes( self.paymentMethodField.val() );
			if( paymentMethodChanged ) {
				self.paymentMethodField.find( 'option[value="' + paymentMethods[0] + '"]' ).prop( 'selected', true );
				gform.ppcp.stateManager.set( 'paymentMethod', paymentMethods[0] );
			}
		}

		/**
		 * Target of the gform_post_conditional_logic_field_action hook. Updates the state monitor every time conditional logic gets evaluated.
		 *
		 * @since 2.4
		 *
		 * @param {int}    formId         Current form id.
		 * @param {string} action         Possible values are "show" or "hide".
		 * @param {string} targetId       Field id that is being targed by conditional logic.
		 * @param {array}  defaultValues  Array of initial field values.
		 * @param {bool}   isInit         True if this is the initial load of the page. False if not.
		 */
		self.handlePostConditionalLogic = function(formId, action, targetId, defaultValues, isInit) {

			if ( formId != self.formId ) {
				return;
			} else if( targetId == self.GFCCField ) {
				gform.ppcp.stateManager.set( 'fieldVisible', action == 'show' );
			} else if( targetId == self.submitButtonId ) {
				gform.ppcp.stateManager.set( 'submitVisible', action == 'show' );
			}
		}

		/**
		 * Binds hooks, filters and events related to the State Manager.
		 *
		 * @since 2.4
		 */
		self.bindStateEvents = function() {

			// Binding total change event. Set priority to 51 so it will be triggered after the coupons add-on.
			gform.addFilter( 'gform_product_total', self.handleProductTotalFilter, 51 );

			// Binding frontend feed evaluated event.
			gform.addAction( 'gform_frontend_feeds_evaluated', self.handleFrontendFeedsEvaluated );

			// Binding paypal field  and submit button conditional logic event.
			gform.addAction( 'gform_post_conditional_logic_field_action', self.handlePostConditionalLogic );

			// Abort conditional logic action on PayPal Field to prevent field from being briefly displayed, and then hidden when payment amount is 0.
			gform.addFilter( 'gform_abort_conditional_logic_do_action', function( abort, action, targetId, useAnimation, defaultValues, isInit, formId, callback ) {

				if ( formId == self.formId && targetId == self.GFCCField ) {
					abort = true;
				}
				return abort;

			} );

			// Binding currency change.
			$( self.form ).on( 'select change', function(){
				gform.ppcp.stateManager.set( 'currency', self.getCurrentCurrencyCode() );
			} );

			// Binding to payment method change ( i.e. Credit Card or PayPal Checkout ).
			self.paymentMethodField.on( 'change', function() {
				gform.ppcp.stateManager.set( 'paymentMethod', self.paymentMethodField.val() );
				self.resetCardErrors();
			});

		}

		/**
		 * Determines if PayPal Buttons fields have been rendered.
		 *
		 * @since 2.4
		 *
		 * @returns {bool} Returns true if PayPal fields have been rendered. Returns false otherwise.
		 */
		self.isPayPalButtonsRendered = function() {
			return self.paypalButtonInstances.length > 0;
		}

		/**
		 * Renders the PayPal Credit Messages placeholder when enabled.
		 *
		 * @since 2.4
		 *
		 * @param {float} paymentAmount The current amount to be paid.
		 */
		self.renderCreditMessage = function( paymentAmount ) {

			// If setting to display credit message is disable, don't render the message.
			if ( ! self.displayCreditMessages  ){
				return;
			}

			const messagePlaceholder = $( '#gform_wrapper_' +  self.formId + ' #paypal_credit_messages' );

			if ( messagePlaceholder.length ) {
				messagePlaceholder.attr( 'data-pp-amount', paymentAmount );
			}
			else {
				$( '<div id="paypal_credit_messages" data-pp-message data-pp-style-layout="text" data-pp-style-logo-type="inline" data-pp-style-color="black" data-pp-amount="' + paymentAmount + '"></div>' ).prependTo( self.paypalButtons );
			}
		};

		/**
		 * Renders the PayPal Smart buttons and initializes them with the intent and paymentAmount specified.
		 *
		 * @since 2.4
		 *
		 * @param {string} intent         Payment intent configured for the active feed ( i.e. authorize, capture or subscription ).
		 * @param {float}  paymentAmount  Amount to be paid.
		 */
		self.renderPayPalButtons = function( intent, paymentAmount ) {

			// Close buttons if they are already rendered.
			self.closePayPalButtons();

			// Render buttons.
			const btnArgs = self.getPayPalButtonArgs( intent );
			self.updatePayPalScriptIntent( intent );

			let button = paypal.Buttons( btnArgs );
			button.render( '#gform_ppcp_smart_payment_buttons_' + self.formId );

			self.paypalButtonInstances.push(button);

			// Render credit message if enabled.
			self.renderCreditMessage( paymentAmount );
		}

		/**
		 * Returns the payment amount given the active feed and the current total.
		 *
		 * @param {object} activeFeed Current active feed.
		 * @param {float}  total      Form total as displayed on the form
		 *
		 * @since 2.4
		 *
		 * @returns {float} Returns the payment amount. Takes into account which product is configured in the feed.
		 */
		self.getPaymentAmount = function( activeFeed, total ) {
			let paymentAmount = total;

			if ( activeFeed.paymentAmount !== 'form_total' ) {
				var price = GFMergeTag.getMergeTagValue( self.formId, activeFeed.paymentAmount, ':price' ),
					qty = GFMergeTag.getMergeTagValue( self.formId, activeFeed.paymentAmount, ':qty' );

				if (typeof price === 'string') {
					price = GFMergeTag.getMergeTagValue( self.formId, activeFeed.paymentAmount + '.2', ':price' );
					qty = GFMergeTag.getMergeTagValue( self.formId, activeFeed.paymentAmount + '.3', ':qty' );
				}

				paymentAmount = price * qty;
			}

			return paymentAmount;
		}

		/**
		 * Initializes the Credit Card hosted fields.
		 *
		 * This is required even before the fields are displayed, even if they will never be displayed.
		 * A token is generated the first time the fields are rendered,
		 * if the SDK was reloaded ( for instance to change the currency ) before the fields were rendered then token generation fails.
		 *
		 * @since 2.3
		 */
		self.renderCardFields = function() {

			const placeholder_1 = $( '#input_' + self.formId + '_' + self.ccFieldId + '_1' ).text();
			const placeholder_2 = $( '#input_' + self.formId + '_' + self.ccFieldId + '_2' ).text();
			const placeholder_3 = $( '#input_' + self.formId + '_' + self.ccFieldId + '_3' ).text();

			// Clear the placeholder in <span>.
			$( '#input_' + self.formId + '_' + self.ccFieldId + '_1, #input_' + self.formId + '_' + self.ccFieldId + '_2, #input_' + self.formId + '_' + self.ccFieldId + '_3' ).text( '' );

			// Render credit card fields.
			paypal.HostedFields.render(
				{
					createOrder: self.createOrder,
					styles: self.cardStyle,
					fields: {
						number: {
							selector: '#input_' + self.formId + '_' + self.ccFieldId + '_1',
							placeholder: placeholder_1
						},
						cvv: {
							selector: '#input_' + self.formId + '_' + self.ccFieldId + '_3',
							placeholder: placeholder_3
						},
						expirationDate: {
							selector: '#input_' + self.formId + '_' + self.ccFieldId + '_2',
							placeholder: placeholder_2
						}
					}
				}
			).then(
				function( hf ) {
					window.__gforms_hf = hf;
					$( self.form ).attr( 'data-ppcp-card-rendered', true );

					hf.on(
						'focus',
						function( event ) {
							if ( event.emittedBy === 'number' ) {
								wp.a11y.speak(
									$( '#field_' + self.formId + '_' + self.ccFieldId + '_supported_creditcards' ).text()
								);
							}
						}
					);

					hf.on(
						'cardTypeChange',
						function(event) {
							let cardType;
							const cardContainer = $( '#input_' + self.formId + '_' + self.ccFieldId + '_1' ).parents( '.gfield' ).find( '.gform_card_icon_container' );

							if ( event.cards.length !== 1 ) {
								cardType = null;
							} else {
								cardType = event.cards[0].niceType.toLowerCase();

								if ( cardType === 'american express' ) {
									cardType = 'amex';
								}
							}

							if ( ! cardType ) {
								$( cardContainer ).find( '.gform_card_icon' ).removeClass( 'gform_card_icon_selected gform_card_icon_inactive' );
							} else {
								// Display the check on card icons.
								$( cardContainer ).find( '.gform_card_icon' ).removeClass( 'gform_card_icon_selected' ).addClass( 'gform_card_icon_inactive' );
								$( cardContainer ).find( '.gform_card_icon_' + cardType ).removeClass( 'gform_card_icon_inactive' ).addClass( 'gform_card_icon_selected' );
								// Check if the credit card is supported.
								event.cards[0].type = cardType;
								self.isCardSupported( event.cards[0] );
							}

							const cardTypeInput = $( '#ppcp_credit_card_type_' + self.formId );
							if ( cardTypeInput.length ) {
								cardTypeInput.val( event.cards[0].niceType );
							} else {
								$( self.form ).append( $( '<input type="hidden" name="ppcp_credit_card_type" id="ppcp_credit_card_type_' + self.formId +'" />' ).val( event.cards[0].niceType ) );
							}
						}
					);


				}
			);
		}

		/**
		 * Handles the form submission for the credit card field processing.
		 *
		 * @param {object} event Form submission event.
		 */
		self.handleFormSubmission = function( event ) {

			if ( self.isGoPrevPage() ) {
				self.removeIdFields();
			} else if ( gform.ppcp.stateManager.get( 'paymentMethod' ) == 'Credit Card' && gform.ppcp.stateManager.get( 'activeFeed' ).intent == 'subscription' ) {
				// Credit card checkout is not supported by subscriptions.
				self.displayCardError( gforms_ppcp_frontend_strings.card_info_error );
				self.resetPayPalStatus();
				event.preventDefault();
				return false;
			}

			const continueDefaultFormSubmission =
				! gform.ppcp.stateManager.get( 'activeFeed' ) ||
				gform.ppcp.stateManager.get( 'paymentMethod' ) !== 'Credit Card' ||
				$( '#gform_save_' + self.formId ).val() === '1' ||
				$( self.form ).data( 'gfppcpsubmitting' ) ||
				self.paymentAmount <= 0 ||
				! self.isCreditCardOnPage() ||
				self.isGoPrevPage() ||
				( typeof gformIsRecaptchaPending !== 'undefined' && gformIsRecaptchaPending( $( self.form ) ) );

			if ( continueDefaultFormSubmission ) {
				return true;
			}

			event.preventDefault();

			gformAddSpinner( self.formId );
			$( self.form ).data( 'gfppcpsubmitting', true );

			// Validate CC fields.
			let hf = window.__gforms_hf;
			let state     = hf.getState();
			let formValid = Object.keys( state.fields ).every(
				function( key ) {
					return state.fields[ key ].isValid;
				}
			);

			if ( ! formValid ) {
				// Let the customer know their fields are invalid.
				self.displayCardError( gforms_ppcp_frontend_strings.card_info_error );
				self.resetPayPalStatus();

				return false;
			} else {
				self.resetCardErrors();
			}

			// CC fields are valid, submit hosted fields card data to PayPal.
			let cardData = {
				contingencies: ['SCA_WHEN_REQUIRED']
			};

			// Card holder name is not hidden.
			if ( $( '#input_' + self.formId + '_' + self.ccFieldId + '_5' ).length) {
				cardData.cardholderName = $( '#input_' + self.formId + '_' + self.ccFieldId + '_5' ).val();
			}

			hf.submit( cardData ).then(
				function( payload ) {
					// Needed only when 3-D Secure contingency applied
					if ( payload.liabilityShifted === undefined ) {
						// Handle no 3-D Secure contingency passed scenario
						$( self.form ).submit();
					} else if ( payload.liabilityShifted ) {
						// Handle Buyer confirmed 3-D Secure successfully
						$( self.form ).submit();
					} else {
						if ( payload.authenticationReason === 'SKIPPED_BY_BUYER' ) {
							// Handle buyer skipped 3-D Secure use-case
							const result = confirm( gforms_ppcp_frontend_strings.skipped_by_buyer );
							if ( result ) {
								$( self.form ).submit();
							}
						} else {
							self.displayCardError( gforms_ppcp_frontend_strings.threed_secure_error );
							self.resetPayPalStatus();
						}
					}
				}
			).catch(
				function( error ) {
					self.resetPayPalStatus();
					// Supported card types cannot be detected by the validityChange event,
					// but only after submitting the fields, an error would be thrown.
					if ( error.hasOwnProperty( 'details' ) ) {
						self.displayCardError( error.details[0].description );
					}
				}
			);
		}

		/**
		 * Updates the currently used currency and updates the PayPal currency argument in the SDK url accordingly.
		 *
		 * @since 2.3
		 */
		self.resetPayPalScript = function( currentCurrency, scriptLoadedCallback ) {

			let paypalScript = $( '#gform_paypal_sdk-js' );
			if ( ! paypalScript.length || self.updatingCurrencyInProgress ) {
				return;
			}

			self.updatingCurrencyInProgress = true;

			gformAddSpinner( self.formId );

			// Disable all elements in the form until the new SDK is loaded.
			self.disableForm();

			// Get the current SDK attributes and current UI state of the field.
			var sdkNewCurrencySrc = paypalScript.attr( 'src' ).replace( /currency=\w{3}/ , 'currency=' + currentCurrency );
			var attributionId = paypalScript.attr( 'data-partner-attribution-id' );

			// Remove current script and PayPal instances
			paypalScript.remove();
			self.destroyPayPalButtons();

			// Add a new script with same attributes.
			// Append to head but don't change the src yet so we can hook the onload method.
			$( '<script>' )
				.attr( 'id', 'gform_paypal_sdk-js' )
				.appendTo( 'head' );

			paypalScript = $( '#gform_paypal_sdk-js' );
			paypalScript[0].onload = function() {

				paypalScript.attr('data-identifier', 'gform_ppcp_js_sdk' ).attr( 'data-partner-attribution-id', attributionId );

				self.updatingCurrencyInProgress = false;

				scriptLoadedCallback()
			};

			paypalScript.attr('src', sdkNewCurrencySrc );
		};


		/**
		 * Filters form feeds after conditional logic has been evaluated to get the first PPCP active feed.
		 * If there are more than one active feed for any payment add-on, only the first one is honored.
		 *
		 * @since 2.0
		 *
		 * @param {array} feeds All form feeds.
		 *
		 * @return {boolean|object} The first active feed object or false if no active feeds exist.
		 */
		self.getActiveFeed = function( feeds ) {

			const activePPCPFeeds = feeds.filter( function( feed ) {
				return feed.addonSlug === 'gravityformsppcp' && feed.isActivated;
			});

			if ( activePPCPFeeds.length == 0 ) {
				return false;
			}

			const activeFeeds = Object.values( self.feeds ).filter( function( feed ) {
				return feed.feedId == activePPCPFeeds[0].feedId;
			})

			return activeFeeds.length ? activeFeeds[0] : false;

		};

		/**
		 * Checks if the payment method selector should be hidden according to the feed's supported payment methods.
		 *
		 * Subscription feeds do not support credit card field as they are not supported by the subscriptions API
		 *
		 * @since 2.4
		 *
		 * @param {array} paymentMethods Array of available payment methods.
		 */
		self.showHidePaymentMethodSelector = function( paymentMethods ) {

			if ( paymentMethods.length <= 1) {
				self.paymentMethodField.hide();
				return false;
			} else {
				self.paymentMethodField.show();
				return true;
			}
		};

		/**
		 * Builds the smart buttons arguments array according to feed type.
		 *
		 * @since 2.0
		 *
		 * @param {string} intent The payment intent condigured by the current active feed (i.e. capture, authorize or subscription)
		 */
		self.getPayPalButtonArgs = function( intent ) {

			const btnArgs = {
				style: {
					layout: self.smartPaymentButtons.buttonsLayout,
					color: self.smartPaymentButtons.buttonsColor,
					shape: self.smartPaymentButtons.buttonsShape,
				},
				onInit: function( buttonProperties ) {
					self.enableForm();
				},
				onError: function( error ) {
					console.log( error )
				}
			};

			if ( intent === 'authorize' || intent === 'capture' ) {
				btnArgs.onApprove = self.onOrderApprove;
				btnArgs.createOrder = self.createOrder;
			} else if ( intent === 'subscription' ) {
				btnArgs.createSubscription = self.createSubscription;
				btnArgs.onApprove = self.onSubscriptionApprove;
				btnArgs.onError = self.onSubscriptionError;
				btnArgs.onCancel = self.onSubscriptionCancel;
				btnArgs.fundingSource = self.PayPalFunding;
			}

			return btnArgs;

		};

		/**
		 * Sets the PayPal script src intent attribute according to feed type.
		 *
		 * @since 2.0
		 *
		 * @param {string} intent The payment intent condigured by the current active feed (i.e. capture, authorize or subscription)
		 */
		self.updatePayPalScriptIntent = function( intent ) {
			const scriptTag = $( 'script[data-identifier="gform_ppcp_js_sdk"]' );

			// Sometimes the SDK hasn't been loaded yet, return as this will be called again once the SDK completes loading.
			if ( scriptTag.length <= 0 ) {
				return;
			}

			let src = scriptTag.attr( 'src' );
			// Reset intent.
			src = src.replace( '&intent=authorize', '' ).replace( '&intent=subscription', '' ).replace( '&vault=true', '' ).replace( '&intent=capture', '' );
			if ( intent === 'authorize' || intent === 'capture' ) {
				src += '&intent=' + intent;
			} else if ( intent === 'subscription' ) {
				src += '&vault=true&intent=subscription';
			}
			scriptTag.attr( 'src', src );
		};

		/**
		 * Applied the class name "gform_ppcp_field_price" to fields that
		 * affect the conditional logic.
		 */
		self.applyPriceClass = function() {
			if ( window['gf_form_conditional_logic'] === undefined || window['gf_form_conditional_logic'][ self.formId ] === undefined ) {
				return;
			}

			const fieldsKey   = Object.keys( window['gf_form_conditional_logic'][ self.formId ].fields );
			const fieldsValue = Object.values( window['gf_form_conditional_logic'][ self.formId ].fields );
			let fields        = [];

			for ( let i = 0; i < fieldsValue.length; i++ ) {
				if ( fieldsValue[ i ].length ) {
					fields.push( fieldsKey[ i ] );
				}
			}

			for ( let i = 0; i < fields.length; i++ ) {
				$( '#field_' + self.formId + '_' + fields[ i ] ).not( '.gfield_price' ).addClass( 'gform_ppcp_field_price' );
			}
		}

		self.getDiscountTotal = function() {
			/**
			 * gform_discount_total
			 *
			 * Modify the discount total value.
			 *
			 * @since 2.1
			 *
			 * @param {float}  discountTotal The calculated positive discount total value.
			 * @param {int}    formId        The current form id.
			 * @param {float}  Total         The form total value.
			 *
			 * @return {float} The modified discount total.
			 */
			return gformRoundPrice( Math.abs( gform.applyFilters( 'gform_ppcp_discount_total', gformRoundPrice( self.discountTotal ), self.formId, gformRoundPrice( self.paymentAmount ) ) ) );
		};

		self.getOrderData = function() {
			self.items         = [];
			self.subTotal      = self.shipping = 0;
			self.total         = 0;
			self.discountTotal = 0;

			$.ajax(
				{
					async: false,
					url: gforms_ppcp_frontend_strings.ajaxurl,
					dataType: 'json',
					method: 'POST',
					data: {
						action: "gfppcp_get_order_data",
						nonce: gforms_ppcp_frontend_strings.create_order_nonce,
						form_id: self.formId,
						feed_id: gform.ppcp.stateManager.get('activeFeed').feedId,
						data: $( self.form ).serialize(),
					},
					success: function( response ) {
						if ( response.success ) {
							self.items         = response.data.items;
							self.subTotal     = gformRoundPrice( response.data.subTotal );
							self.total      = gformRoundPrice( response.data.total );
							self.discountTotal = Math.abs( gformRoundPrice( response.data.discountTotal ) );
							//gformUpdateTotalFieldPrice( self.formId, self.total );
							self.shipping = gformRoundPrice( response.data.shipping );
						}
					}
				}
			);
		};

		self.getMergeTagValue = function( field ) {
			if ( field === '' ) {
				return '';
			} else {
				return GFMergeTag.replaceMergeTags( self.formId, '{:' + field + '}' );
			}
		};

		self.updateOrderDetails = function() {
			const activeFeed = gform.ppcp.stateManager.get('activeFeed');

			let payer = {
				name: {
					given_name: self.getMergeTagValue( activeFeed.first_name ),
					surname: self.getMergeTagValue( activeFeed.last_name )
				}
			};

			if ( self.countryCode !== '' ) {
				payer.address = {
					address_line_1: self.getMergeTagValue( activeFeed.address_line1 ),
					address_line_2: self.getMergeTagValue( activeFeed.address_line2 ),
					admin_area_2: self.getMergeTagValue( activeFeed.address_city ),
					admin_area_1: self.getMergeTagValue( activeFeed.address_state ),
					postal_code: self.getMergeTagValue( activeFeed.address_zip ),
					country_code: self.countryCode
				};
			}

			if ( self.getMergeTagValue( activeFeed.email ) !== '' ) {
				payer.email_address = self.getMergeTagValue( activeFeed.email );
			}
			const shipping      = ( activeFeed.no_shipping === '1' ) ? 'NO_SHIPPING' : 'GET_FROM_FILE';
			let shippingDetails = ( activeFeed.no_shipping === '0' ) ? {
				name: {
					full_name: self.getMergeTagValue( activeFeed.first_name ) + ' ' + self.getMergeTagValue( activeFeed.last_name )
				}
			} : {};

			if ( shipping !== 'NO_SHIPPING' && self.countryCode !== '' ) {
				shippingDetails.address = payer.address;
			}

			let breakdown = {
				item_total: {
					value: gformRoundPrice( self.subTotal ).toString(),
					currency_code: self.getCurrentCurrencyCode(),
				},
				shipping: {
					value: gformRoundPrice( self.shipping ).toString(),
					currency_code: self.getCurrentCurrencyCode(),
				}
			};

			// Get discount.
			const discountTotal = self.getDiscountTotal();
			if ( discountTotal > 0 ) {
				breakdown.discount = {
					value: Math.abs( discountTotal ).toString(),
					currency_code: self.getCurrentCurrencyCode(),
				};
			}

			// Make sure items have the same currency as order currency.
			for ( var orderItem of self.items ) {
				orderItem.unit_amount.currency_code = self.getCurrentCurrencyCode();
			}

			const purchase_units = [{
					amount: {
						value: gformRoundPrice( self.paymentAmount ).toString(),
						currency_code: self.getCurrentCurrencyCode(),
						breakdown: breakdown
					},
					description: activeFeed.feedName,
					items: self.items
				}];


			if ( shipping !== 'NO_SHIPPING' ) {
				purchase_units[0].shipping = shippingDetails;
			}

			return {
				payer: payer,
				purchase_units: purchase_units,
				application_context: {
					shipping_preference: shipping
				}
			};
		};

		self.createOrder = async function() {
			let orderID = '';

			if ( self.orderIdField.length ) {
				return self.orderIdField.val();
			}

			self.countryCode = self.getMergeTagValue( gform.ppcp.stateManager.get('activeFeed').address_country );
			if ( self.countryCode !== '' ) {
				// It could just be a country value, but not code yet.
				await self.getCountryCode( self.countryCode );
			}

			await self.getOrderData();

			await fetch(
				gforms_ppcp_frontend_strings.ajaxurl + '?action=gfppcp_create_order',
				{
					method: 'POST',
					headers: {
						'content-type': 'application/json'
					},
					body: JSON.stringify(
						{
							nonce: gforms_ppcp_frontend_strings.create_order_nonce,
							data: self.updateOrderDetails(),
							form_id: self.formId,
							feed_id: gform.ppcp.stateManager.get('activeFeed').feedId
						}
					)
				}
			).then(
				function( res ) {
					return res.json();
				}
			).then(
				function( response ) {
					if ( response.success ) {
						orderID = response.data.orderID;
						$( self.form ).append( $( '<input type="hidden" id="gf_ppcp_order_id_' + self.formId + '" name="ppcp_order_id" />' ).val( orderID ) );
						self.resetCardErrors();
					} else {
						self.displayCardError( response.data.message );
						self.resetPayPalStatus();
						return false;
					}
				}
			);

			return orderID;
		}

		self.onOrderApprove = function( data, actions ) {
			gformAddSpinner( self.formId );

			$( self.form ).submit();
		}



		/**
		 * Disable all elements in the form.
		 *
		 * This is needed to prevent any changes in the form that might break ongoing operations like rendering the smart buttons or reloading the SDK.
		 *
		 * @since 2.3
		 */
		self.disableForm = function() {
			if ( $( self.form ).data( 'gform-disabled' ) ) {
				return;
			}

			$( self.form )
				.data( 'gform-disabled', true )
				.find('input, select, textarea')
				.each(
					function() {
						$( this ).data( 'gform-disabled-initial-value', $( this ).prop( 'disabled' ) );
						$( this ).prop( 'disabled', true );
					}
				);
		};

		/**
		 * Resets all elements to their initial disabled status before calling disableForm().
		 *
		 * @since 2.3
		 */
		self.enableForm = function() {

			if ( ! $( self.form ).data( 'gform-disabled' ) ) {
				return;
			}

			$( self.form )
				.data('gform-disabled', false )
				.find('input, select, textarea')
				.each(
					function() {
						$( this ).prop( 'disabled', $( this ).data( 'gform-disabled-initial-value' ) );
						$( this ).removeAttr( 'gform-disabled-initial-value' );
					}
				);
		};

		/**
		 * Creates a PayPal subscription.
		 *
		 * @since 2.0
		 *
		 * @param {Object} data
		 * @param {Object} actions
		 */
		self.createSubscription = async function( data, actions ) {
			gformAddSpinner( self.formId );

			// If we already have a subscription id, submit form.
			if ( self.subscriptionIdField.length ) {
				$( self.form ).submit();
			}

			// Get plan ID  or create a new one if no plan matches the current subscription properties.
			let subscriptionRequest = '';
			await fetch(
				gforms_ppcp_frontend_strings.ajaxurl + '?action=gfppcp_create_subscription',
				{
					method: 'POST',
					headers: {
						'content-type': 'application/json',
					},
					body: JSON.stringify(
						{
							nonce: gforms_ppcp_frontend_strings.create_subscription_nonce,
							form_data: $( self.form ).serialize(),
							form_id: self.formId,
							feed_id: gform.ppcp.stateManager.get('activeFeed').feedId,
						}
					),
				}
			).then(
				function( res ) {
					return res.json();
				}
			).then(
				function( response ) {
					if ( response.success ) {
						subscriptionRequest = response.data;
						self.resetCardErrors();
					} else {
						self.displayCardError( response.data.message );
						self.resetPayPalStatus();
						return false;
					}
				}
			);

			subscriptionRequest.application_context.user_action = 'CONTINUE';
			return actions.subscription.create( subscriptionRequest );
		};

		/**
		 * Fires when user approves a subscription created using smart buttons.
		 *
		 * @since 2.0
		 *
		 * @param {Object} data
		 * @param {Object} actions
		 */
		self.onSubscriptionApprove = function( data, actions ) {
			// Appeand created subscription ID to form.
			if ( self.subscriptionIdField.length === 0 ) {
				$( self.form ).append( $( '<input type="hidden" id="gf_' + self.formId + '_ppcp_subscription_id" name="ppcp_subscription_id" />' ).val( data.subscriptionID ) );
			}

			$( self.form ).submit();
		};

		/**
		 * Fires when an error ocures while creating a subscription using smart buttons.
		 *
		 * @since 2.0
		 */
		self.onSubscriptionError = function() {
			self.displayCardError( gforms_ppcp_frontend_strings.catch_all_error );
		};

		/**
		 * Fires when the user cancels creating a subscription using smart buttons.
		 *
		 * @since 2.0
		 */
		self.onSubscriptionCancel = function() {
			self.resetCardErrors();
			self.hideFormSpinner();
		};

		self.getCountryCode = function( country ) {
			return $.ajax(
				{
					async: false,
					url: gforms_ppcp_frontend_strings.ajaxurl,
					dataType: 'json',
					method: 'POST',
					data: {
						action: "gfppcp_get_country_code",
						nonce: gforms_ppcp_frontend_strings.on_approve_nonce,
						country: country,
						feed_id: gform.ppcp.stateManager.get('activeFeed').feedId
					},
					success: function( response ) {
						if ( response.success ) {
							self.countryCode = response.data.code;
						}
					}
				}
			);
		};

		self.isCreditCardOnPage = function() {
			const currentPage = self.getCurrentPageNumber();

			// if current page is false or no credit card page number, assume this is not a multi-page form.
			if ( ! self.ccPage || ! currentPage ) {
				return true;
			}

			return self.ccPage === currentPage;
		};

		self.getCurrentPageNumber = function() {
			const currentPageInput = $( '#gform_source_page_number_' + self.formId );

			return currentPageInput.length > 0 ? parseInt( currentPageInput.val(), 10 ) : false;
		};

		self.isLastPage = function() {
			const targetPageInput = $( '#gform_target_page_number_' + self.formId );
			if ( targetPageInput.length > 0 ) {
				return targetPageInput.val() === '0';
			}

			return true;
		};

		self.isCardSupported = function( card ) {
			if ( $.inArray( card.type, Object.keys( window['gf_cc_rules'] ) ) === -1 ) {
				self.displayCardError( card.niceType + ' ' + gforms_ppcp_frontend_strings.card_not_supported );
			} else {
				self.resetCardErrors();
			}
		};

		self.displayCardError = function( message ) {
			if ( typeof message === 'undefined' ) {
				message = '';
			}

			let messageContainer = self.getMessageContainerSelector();

			if ( ! $( messageContainer ).find( '.validation_message' ).length ) {
				$( messageContainer ).append( '<div class="gfield_description validation_message"></div>' );
			}

			var cardErrors = self.getCardErrors();

			if ( message !== '' ) {
				$( messageContainer ).addClass( 'gfield_error' );
				$( cardErrors[ 0 ] ).html( message ).show();
				// Announce the error to the screen reader.
				wp.a11y.speak( message, 'assertive' );
				self.hideFormSpinner();

				// Display main field so that error is not hidden.
				self.showHidePayPalField( 'block' );
			}
		};

		/**
		 * Resets field error state and removes any displayed errors.
		 *
		 * @since 2.0
		 */
		self.resetCardErrors = function() {
			const messageContainer = self.getMessageContainerSelector();
			const cardErrors = self.getCardErrors();
			$( messageContainer ).removeClass( 'gfield_error' );
			$( cardErrors[ 0 ] ).html( '' ).hide();
		}

		/**
		 * Gets the selector of the container div in which an error message will be displayed.
		 *
		 * @since 2.0
		 *
		 * return {string}
		 */
		self.getMessageContainerSelector = function() {
			return self.GFCCField;
		}

		/**
		 * Gets a list of error elements within the card message container.
		 *
		 * @since 2.0
		 *
		 * @return {jQuery} an array of elements that contain error messages.
		 */
		self.getCardErrors = function() {
			var messageContainer = self.getMessageContainerSelector();
			return $( messageContainer ).find( '.validation_message' );
		}

		/**
		 * Hides the form spinner that appears next to submit button.
		 *
		 * @since 2.0
		 */
		self.hideFormSpinner = function() {
			if ( $( '#gform_ajax_spinner_' + self.formId ).length > 0 ) {
				$( '#gform_ajax_spinner_' + self.formId ).remove();
			}
		}

		self.resetPayPalStatus = function() {
			$( self.form ).data( 'gfppcpsubmitting', false );
			self.hideFormSpinner();

			// must do this or the form cannot be submitted again
			if ( self.isLastPage() ) {
				window[ 'gf_submitting_' + self.formId ] = false;
			}
		};

		self.isGoPrevPage = function() {
			// don't create card token if clicking on the Previous button.
			const sourcePage = parseInt( $( '#gform_source_page_number_' + self.formId ).val(), 10 );
			const targetPage = parseInt( $( '#gform_target_page_number_' + self.formId ).val(), 10 );

			return sourcePage > targetPage && targetPage !== 0;
		};

		/**
		 * Removes PayPal order ID and subscription ID fields when order details change.
		 *
		 * @since 2.0
		 */
		self.removeIdFields = function() {
			self.orderIdField.remove();
			self.subscriptionIdField.remove();
		};

		/**
		 * Returns the currently used currency code.
		 *
		 * @since 2.3
		 *
		 * @return {string}
		 */
		self.getCurrentCurrencyCode = function() {
			var code = '';
			var currentCurrency = new Currency( gf_global.gf_currency_config );

			if ( 'getCode' in currentCurrency === false || typeof currentCurrency.getCode !== 'function' ) {
				var supportedCurrencies = gforms_ppcp_frontend_strings.currencies;
				for( var supportedCurrencyCode of Object.keys( supportedCurrencies ) ) {
					if ( supportedCurrencies[ supportedCurrencyCode ].name === gf_global.gf_currency_config.name ) {
						code = supportedCurrencyCode;
						break;
					}
				}
			} else {
				code = currentCurrency.getCode();
			}

			if ( ! code ) {
				return self.currency;
			}

			return code;
		};

		/**
		 * Checks if paypal instances exist and destroys them if they are fully loaded.
		 *
		 * @since 2.3
		 */
		self.destroyPayPalButtons = function() {

			try {

				var buttonsClosed = false;
				while ( buttonsClosed === false ) {
					buttonsClosed = self.closePayPalButtons();
				}

				// delete all paypal related instances.
				Object.keys( window ).forEach( ( key ) => {
					if ( /paypal|zoid|post_robot/.test( key ) ) {
						delete window[ key ];
					}
				});

				self.paypalButtons.html('');

			} catch( e ) {
				return;
			}

		};

		self.closePayPalButtons =  function() {

			if ( self.paypalButtonInstances.length == 0 ) {
				return true;
			}

			for( var buttonInstance of self.paypalButtonInstances ) {
				try {
					buttonInstance.close();
				} catch ( e ) {
					return false;
				}
			}

			return true;
		};

		self.init();
	};

})( jQuery );
